body {
  position: absolute;
  height: 100%;
  width: 100%;
  font-size : 1em;
  
  margin: 0;
  padding: 0;
  font-family: 'Do Hyeon', BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  position: absolute;
  height: 100%;
  width: 100%;
}

